  :root {
    /* --- CLEAN CLINICAL ROYAL BLUE THEME --- */

    /* BACKGROUND (PURE WHITE) */
    --background: 0 0% 100%;
    --foreground: 225 30% 18%;
    /* Deep medical navy text */

    /* SURFACES */
    --card: 0 0% 100%;
    --card-foreground: 225 30% 18%;

    --popover: 0 0% 100%;
    --popover-foreground: 225 30% 18%;

    /* BRAND PRIMARY — Royal Medical Blue (#1B3C7A) */
    --primary: 220 60% 32%;
    --primary-foreground: 0 0% 100%;

    /* SECONDARY — Professional Blue (#2F6FED) */
    --secondary: 220 85% 65%;
    --secondary-foreground: 0 0% 100%;

    /* ACCENT — Clinical Aqua (#4DD0E1) */
    --accent: 185 60% 65%;
    --accent-foreground: 225 30% 18%;

    /* MUTED */
    --muted: 220 35% 94%;
    --muted-foreground: 225 25% 40%;

    /* DESTRUCTIVE */
    --destructive: 0 70% 55%;
    --destructive-foreground: 0 0% 100%;

    /* BORDERS (Soft frosted blue-gray) */
    --border: 220 30% 86%;
    --input: 220 30% 82%;
    --ring: 220 60% 32%;

    /* RADIUS */
    --radius: 0.5rem;
  }

  /* --- DARK MODE (DEEP ROYAL MEDICAL BLUE) --- */
  .dark {
    /* PURE BLACKISH MEDICAL MODE */
    --background: 220 15% 8%;
    /* charcoal black */
    --foreground: 0 0% 98%;
    /* almost pure white */

    --card: 220 15% 12%;
    /* deep charcoal */
    --card-foreground: 0 0% 98%;

    --popover: 220 15% 12%;
    --popover-foreground: 0 0% 98%;

    /* PRIMARY BUTTONS STILL ROYAL BLUE */
    --primary: 220 70% 55%;
    --primary-foreground: 0 0% 100%;

    /* SECONDARY BLUE SHADE */
    --secondary: 220 25% 25%;
    /* muted dark blue-gray */
    --secondary-foreground: 0 0% 98%;

    /* ACCENT POP (stays aqua) */
    --accent: 185 70% 60%;
    --accent-foreground: 220 15% 8%;

    /* MUTED GRAYS */
    --muted: 220 10% 18%;
    --muted-foreground: 0 0% 75%;

    /* DESTRUCTIVE */
    --destructive: 0 70% 55%;
    --destructive-foreground: 0 0% 98%;

    /* BORDERS — subtle gray */
    --border: 220 10% 28%;
    --input: 220 10% 28%;
    --ring: 220 70% 55%;
  }
  .border {
  border-color: hsl(var(--border));
}

  body {
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
